import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import instagramIcon from '../../assets/images/instagram.svg';
import vkIcon from '../../assets/images/vk.svg';
import telegramIcon from '../../assets/images/telegram.svg';
import facebookIcon from '../../assets/images/facebook.svg';
import { sendContactMessage } from '../../utils/apiService';
import './ConnectSection.css';

const ConnectSection: React.FC = () => {
  //      
  const [message, setMessage] = useState<string>(''); //    
  const [email, setEmail] = useState<string>(''); //    email

  //   
  const handleSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();

    // ,     
    if (!email || !message) {
      alert(',   .');
      return;
    }

    //    
    const formData = { email, message };

    try {
      //    
      await sendContactMessage(formData);
      alert('  !');
      setEmail(''); //   email
      setMessage(''); //   message
    } catch (error) {
      alert(error instanceof Error ? error.message : '    .');
    }
  };

  return (
    <section className="connect__section">
      <div className="container flex flex-column">
        {/*   */}
        <div className="connect__section__title flex">
          <div></div>
          <h1 className="title">
             <br />
             
          </h1>
        </div>
        <div className="connect__section__social__media__form flex">
          {/*   */}
          <div className="connect__section__social__media flex flex-column justify-between">
            <div>
              <p>     </p>
              <div className="flex">
                {/*       */}
                <a
                  href="https://www.instagram.com"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <img src={instagramIcon} alt="Instagram" />
                </a>
                <a
                  href="https://vk.com"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <img src={vkIcon} alt="VK" />
                </a>
                <a
                  href="https://telegram.org"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <img src={telegramIcon} alt="Telegram" />
                </a>
                <a
                  href="https://www.facebook.com"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <img src={facebookIcon} alt="Facebook" />
                </a>
              </div>
            </div>
            {/*     cookie */}
            <div>
              <p>
                <Link to="/privacy-policy"> </Link>
              </p>
              <p>
                <Link to="/cookie-policy">   cookie</Link>
              </p>
            </div>
          </div>

          {/*    */}
          <div className="connect__section__form">
            <p>
              ,  ,        .
            </p>
            <form className="flex flex-column" onSubmit={handleSubmit}>
              {/*     */}
              <label htmlFor="message" className="form-label">
                    ?
              </label>
              <textarea
                id="message"
                className="form-input large-input"
                value={message}
                onChange={(e) => setMessage(e.target.value)} //      
              ></textarea>

              {/*    email */}
              <label htmlFor="email" className="form-label">
                 E-mail?
              </label>
              <input
                type="email"
                id="email"
                className="form-input small-input"
                value={email}
                onChange={(e) => setEmail(e.target.value)} //      
                required
              />

              {/*     */}
              <button type="submit" className="button__without__bg">
                
              </button>
            </form>
          </div>
        </div>
      </div>
    </section>
  );
};

export default ConnectSection;
