import React, { useState, ChangeEvent, FormEvent } from 'react';
import { AxiosError } from 'axios';
import axiosInstance from '../../utils/axiosInstance';
import { useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify';
import { LoginFormData } from '../../types';
import { loginUser } from '../../utils/apiService';
import './LoginForm.css';

const apiUrl = process.env.REACT_APP_API_URL;

//  LoginForm      
const LoginForm: React.FC = () => {
  //     
  const [formData, setFormData] = useState<LoginFormData>({
    username: '',
    password: '',
  });

  //     
  const navigate = useNavigate();

  //    
  const handleChange = (e: ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  //   
  const handleSubmit = async (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    try {
      //    backend  
      const response = await axiosInstance.post(`${apiUrl}/api/users/login`, formData);
      console.log(' :', response.data);
      const token = await loginUser(formData);

      //    localStorage
      localStorage.setItem('token', response.data.token);
      toast.success(' !'); //  
      navigate('/edit-profile');
    } catch (error) {
      //   
      const err = error as AxiosError;
      if (err.response && err.response.data) {
        toast.error((err.response.data as { message: string }).message); //     
      } else {
        toast.error('  ,   ');
      }
    }
  };

  return (
    <form onSubmit={handleSubmit}>
      {/*     */}
      <div className="form__field centered-input-wrapper">
        <div className="form__input centered-input">
          <input
            type="text"
            name="username"
            placeholder=" "
            className="default__input form__post"
            value={formData.username}
            onChange={handleChange}
            required
          />
        </div>
      </div>

      {/*    */}
      <div className="form__field centered-input-wrapper">
        <div className="form__input centered-input">
          <input
            type="password"
            name="password"
            placeholder=""
            className="default__input form__post"
            value={formData.password}
            onChange={handleChange}
            required
          />
        </div>
      </div>

      {/*     */}
      <button className="button__with__bg form__post" type="submit">
        
      </button>
    </form>
  );
};

export default LoginForm;
