import React, { useRef, useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import rightArrow from '../../assets/images/right-arrow.svg';
import likeIcon from '../../assets/images/like.svg';
import dislikeIcon from '../../assets/images/dislike.svg';
import starIcon from '../../assets/images/star.svg';
import commentsIcon from '../../assets/images/comments.svg';
import { ArticleDetail } from '../../types';
import { fetchPopularArticles } from '../../utils/apiService';
import './ArticlesSection.css';

const ArticlesSection: React.FC = () => {
  const [loading, setLoading] = useState<boolean>(true);
  //  useRef    
  const sliderRef = useRef<HTMLDivElement | null>(null);
  //       
  const [showPrev, setShowPrev] = useState<boolean>(false);
  const [showNext, setShowNext] = useState<boolean>(true);
  //     
  const [articles, setArticles] = useState<ArticleDetail[]>([]);

  //      
  const updateButtonVisibility = () => {
    const slider = sliderRef.current;
    if (slider) {
      setShowPrev(slider.scrollLeft > 0); //   "",    
      setShowNext(slider.scrollLeft + slider.clientWidth < slider.scrollWidth); //   "",    
    }
  };

  //      
  const handleNextClick = () => {
    const slider = sliderRef.current;
    if (slider) {
      slider.scrollBy({
        left: slider.clientWidth,
        behavior: 'smooth',
      });
    }
  };

  //      
  const handlePrevClick = () => {
    const slider = sliderRef.current;
    if (slider) {
      slider.scrollBy({
        left: -slider.clientWidth,
        behavior: 'smooth',
      });
    }
  };

  // useEffect      
  useEffect(() => {
    const loadArticles = async () => {
      try {
        const popularArticles = await fetchPopularArticles(3); //  
        setArticles(popularArticles); //    
      } catch (error) {
        console.error('    :', error);
      } finally {
        setLoading(false);
      }
    };

    loadArticles(); //     
  }, []);

  // useEffect       
  useEffect(() => {
    const slider = sliderRef.current;
    if (slider) {
      slider.addEventListener('scroll', updateButtonVisibility);
      updateButtonVisibility();  //    
    }
    return () => {
      if (slider) {
        slider.removeEventListener('scroll', updateButtonVisibility);
      }
    };
  }, []);
  
  if (loading) {
    return <div>  ...</div>;
  }

  return (
    <section className="articles__section">
      <div className="container">
        <div className="articles__section__block">
          <div className="articles__section__block__title flex">
            <div></div>
            <h1 className="title"> <br /> </h1>
          </div>
          <div className="slider-container">
            <div className="articles__section__block__cards flex" ref={sliderRef}>
              {/*     */}
              <button
                className={`slider-button prev ${showPrev ? 'show' : ''}`}
                onClick={handlePrevClick}
                style={{ display: showPrev ? 'block' : 'none' }}
              >
                <img src={rightArrow} alt="" />
              </button>

              {/*   */}
              {articles.map((article) => (
                <div className="articles__section__block__card" key={article.id}>
                  <div>
                    <img
                      src={article.coverImage}
                      alt={article.title}
                      className="articles__section__block__card__img"
                    />
                    <h4>{article.author.name}</h4>
                    <div className="articles__block__card__activities flex">
                      <div className="flex">
                        <img src={likeIcon} alt="" />
                        <p>{article.likes || 0}</p> {/*  likes ,  0 */}
                      </div>
                      <div className="flex">
                        <img src={dislikeIcon} alt="" />
                        <p>{article.dislikes || 0}</p> {/*  dislikes ,  0 */}
                      </div>
                      <div className="flex">
                        <img src={starIcon} alt="" />
                        <p>{article.favoriteCount || 0}</p> {/*  stars ,  0 */}
                      </div>
                      <div className="flex">
                        <img src={commentsIcon} alt="" />
                        <p>{article.comments && article.comments.length === 0 ? (
                          '0'
                          ) : (
                            article.comments?.length
                            )}
                        </p>
                      </div>
                    </div>
                    <p>{article.description}</p>
                  </div>
                  <Link className="button__without__bg" to={`/articles/${article.id}`}>
                     
                  </Link>
                </div>
              ))}

              {/*     */}
              <button
                className={`slider-button next ${showNext ? 'show' : ''}`}
                onClick={handleNextClick}
                style={{ display: showNext ? 'block' : 'none' }}
              >
                <img src={rightArrow} alt="" />
              </button>
            </div>
          </div>
          {/*       */}
          <div className="articles__block__card__btn">
            <Link className="button__with__bg" to="/articles">
               
            </Link>
          </div>
        </div>
      </div>
    </section>
  );
};

export default ArticlesSection;
