import React, { useState, useEffect } from 'react';
import { toast } from 'react-toastify';
import { fetchUserArticles } from '../utils/apiService'; //      
import ArticleCard from '../components/ArticlesPage/ArticleCard'; //    

const UserArticles: React.FC = () => {
  const [articles, setArticles] = useState<any[]>([]); //   
  const [loading, setLoading] = useState<boolean>(true); //  

  //      
  useEffect(() => {
    const loadArticles = async () => {
      const token = localStorage.getItem('token');
      if (!token) {
        toast.error(' ');
        return;
      }

      try {
        const articlesData = await fetchUserArticles(); //   
        setArticles(articlesData); //    
      } catch (error) {
        toast.error('   ');
      } finally {
        setLoading(false); //  
      }
    };

    loadArticles(); //  
  }, []);

  //   
  if (loading) {
    return <p> ...</p>;
  }

  return (
    <div className="articles__div">
      <h1> </h1>

      {articles.length === 0 ? (
        <p>   .</p>
      ) : (
        <div className="articles__block__cards__div">
          <div className="articles__block__cards flex">
            {articles.map((article) => (
              <ArticleCard key={article.id} article={article} />
            ))}
          </div>
        </div>
      )}

      <button className="button__with__bg" style={{ marginTop: '50px' }}>
         
      </button>
    </div>
  );
};

export default UserArticles;
