import React from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom'; //       
import { ToastContainer } from 'react-toastify'; //  ToastContainer   
import 'react-toastify/dist/ReactToastify.css'; //   

import Header from './components/Header'; //  (,  )
import MainContent from './pages/MainContent'; //  ,    
import Footer from './components/Footer'; // ,    
import ArticlesPage from './pages/ArticlesPage'; //    
import ArticleDetailPage from './pages/ArticleDetailPage'; //      
import NewsBestPage from './pages/NewsBestPage'; //    
import NewDetailPage from './pages/NewDetailPage'; //      
import NewsNewestPage from './pages/NewsNewestPage'; //     
import EditProfilePage from './pages/EditProfilePage'; //    
import RegisterPage from './pages/RegisterPage'; //  
import UserFavorites from './pages/UserFavorites'; //   
import UserArticles from './pages/UserArticles'; //   
import LoginPage from './pages/LoginPage'; //  
import ProtectedRoute from './components/ProtectedRoute'; //    
import AuthenticatedRoute from './components/AuthenticatedRoute'; //     
import CreateArticleForm from './components/CreateArticleForm'; //   
import CreateNewForm from './components/CreateNewForm'; //   

import './styles/main.css'; //  
import './styles/media.css'; //     

//   
const App: React.FC = () => {
  return (
    <Router> {/*      Router    */}
      <Header /> {/*  , ,  ,   */}
      <ToastContainer /> {/*    Toastify,     */}
      
      {/*     */}
      <Routes>
        <Route path="/" element={<MainContent />} /> {/*   */}
        <Route path="/articles" element={<ArticlesPage />} /> {/*    */}
        <Route path="/articles/:id" element={<ArticleDetailPage />} /> {/*   ,      ID  */}
        <Route path="/news-best" element={<NewsBestPage />} /> {/*    */}
        <Route path="/news/:id" element={<NewDetailPage />} /> {/*   ,      ID  */}
        <Route path="/news-newest" element={<NewsNewestPage />} /> {/*    */}
        <Route path="/favorite" element={<UserFavorites />} /> {/*    */}
        <Route path="/my-articles" element={<UserArticles />} />  {/*    */}
        <Route path="/register" element={<RegisterPage />} /> {/*    */}
        <Route path="/login" element={<LoginPage />} /> {/*     */}

        {/*  ,     */}
        
        {/*           'partner'  'admin' */}
        <Route element={<ProtectedRoute allowedRoles={['partner', 'admin']} />}>
          <Route path="/create-article" element={<CreateArticleForm />} />
        </Route>

        {/*        */}
        <Route element={<ProtectedRoute allowedRoles={['admin']} />}>
          <Route path="/create-news" element={<CreateNewForm />} />
        </Route>

        {/*        */}
        <Route element={<AuthenticatedRoute />}>
          <Route path="/edit-profile" element={<EditProfilePage />} />
        </Route>
      </Routes>

      <Footer /> {/*     */}
    </Router>
  );
}

export default App;
