import React, { useState, useEffect, ChangeEvent, FormEvent } from 'react';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import noPhoto from '../assets/images/no-image.png';
import star from '../assets/images/star.svg';
import addFile from '../assets/images/add-file.svg';
import { ProfileData } from '../types';
import { fetchUserProfile, updateUserProfile } from '../utils/apiService';
import './EditProfileForm.css';

const apiUrl = process.env.REACT_APP_API_URL;

const EditProfileForm: React.FC = () => {
  const [profileImage, setProfileImage] = useState<File | null>(null);
  const [portfolioImage, setPortfolioImage] = useState<File | null>(null); //    
  const [formData, setFormData] = useState<ProfileData>({
    email: '',
    password: '',
    name: '',
    city: '',
    activity: '',
    phone: '',
    instagram: '',
    vk: '',
    telegram: '',
    facebook: '',
    about: '',
    receiveNewsletter: false,
    profileImage: '',
    portfolioImage: '',
  });

  //    ID   
  const getUserIdFromToken = (): string | null => {
    const token = localStorage.getItem('token');
    if (token) {
      try {
        const payload = JSON.parse(atob(token.split('.')[1]));
        return payload.user;
      } catch (error) {
        console.error('   :', error);
        return null;
      }
    }
    return null;
  };

  const user = getUserIdFromToken();

  //      
  useEffect(() => {
    if (!user) {
      toast.error(': ID   ');
      return;
    }

    const loadProfile = async () => {
      try {
        const profileData = await fetchUserProfile(user); //    
        console.log(' :', profileData);
        setFormData(profileData);
        setPortfolioImage(null); //     ,    
        toast.success('   ');
      } catch (error) {
        console.error('    :', error);
        toast.error(error instanceof Error ? error.message : '    ');
      }
    };

    loadProfile();
  }, [user]);

  //    
  const handleChange = (e: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    if (e.target instanceof HTMLInputElement && e.target.type === 'checkbox') {
      const { checked } = e.target;
      setFormData((prev) => ({
        ...prev,
        [name]: checked,
      }));
    } else {
      setFormData((prev) => ({
        ...prev,
        [name]: value,
      }));
    }
  };

  //    
  const handleProfileImageChange = (e: ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      if (file.size > 5 * 1024 * 1024) {
        toast.error('  . ,      5MB.');
        return;
      }

      const img = new Image();
      img.src = URL.createObjectURL(file);

      img.onload = () => {
        if (img.width > 1920 || img.height > 1080) {
          toast.error('   . ,       1920x1080.');
          return;
        }

        setProfileImage(file);
        toast.success('   ');
      };
    }
  };

  //     (  )
  const handlePortfolioImageChange = (e: ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      if (file.size > 5 * 1024 * 1024) {
        toast.error('  . ,      5MB.');
        return;
      }

      const img = new Image();
      img.src = URL.createObjectURL(file);

      img.onload = () => {
        if (img.width > 1920 || img.height > 1080) {
          toast.error('   . ,       1920x1080.');
          return;
        }

        setPortfolioImage(file);
        toast.success('   ');
      };
    }
  };

  //   
  const handleProfileUpdate = async () => {
    if (!user) {
      toast.error(': ID   ');
      return;
    }

    try {
      const response = await updateUserProfile(user, formData, profileImage, portfolioImage);
      console.log('  :', response);
      setFormData((prev) => ({
        ...prev,
        ...response, //     
      }));
      toast.success('  ');
    } catch (error) {
      console.error('   :', error);
      toast.error(error instanceof Error ? error.message : '   ');
    }
  };

  //   
  const handleSubmit = (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    handleProfileUpdate();
  };

  return (
    <section className="edit-profile-container edit__profile-form">
      <form onSubmit={handleSubmit}>
        <div className="user-photo-wrap">
          <img
            className="user-photo"
            src={
              profileImage
                ? URL.createObjectURL(profileImage)
                : formData.profileImage
                ? `${apiUrl}/${formData.profileImage}?timestamp=${new Date().getTime()}`
                : noPhoto
            }
            alt="Profile"
          />

          <button
            className="button__without__bg"
            type="button"
          >
            <label htmlFor="profile-image">
               
            </label>
          </button>
          <input
            id="profile-image"
            type="file"
            accept="image/jpeg, image/png"
            hidden
            onChange={handleProfileImageChange}
          />
        </div>
        
        {/*   E-mail */}
        <div className="container">
          <div className="form__field">
            <p>E-Mail</p>
            <div className="form__input">
              <input
                type="email"
                name="email"
                placeholder="E-mail"
                className="default__input"
                value={formData.email ?? ''}
                onChange={handleChange}
              />
              <svg
                width="21"
                height="21"
                viewBox="0 0 21 21"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                {/* SVG path here */}
              </svg>
            </div>
          </div>

          {/*    */}
          <div className="form__field">
            <p> </p>
            <div className="form__input">
              <input
                type="password"
                name="password"
                placeholder=""
                className="default__input"
                value={formData.password ?? ''}
                onChange={handleChange}
              />
              <svg
                width="21"
                height="21"
                viewBox="0 0 21 21"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                {/* SVG path here */}
              </svg>
            </div>
          </div>

          {/*    */}
          <div className="form__field">
            <p></p>
            <div className="form__input">
              <input
                type="text"
                name="name"
                placeholder=""
                className="default__input"
                value={formData.name ?? ''}
                onChange={handleChange}
              />
              <svg
                width="21"
                height="21"
                viewBox="0 0 21 21"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                {/* SVG path here */}
              </svg>
            </div>
          </div>

          {/*    */}
          <div className="form__field">
            <p></p>
            <div className="form__input">
              <input
                type="text"
                name="city"
                placeholder=""
                className="default__input"
                value={formData.city ?? ''}
                onChange={handleChange}
              />
              <svg
                width="21"
                height="21"
                viewBox="0 0 21 21"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                {/* SVG path here */}
              </svg>
            </div>
          </div>

          {/*     */}
          <div className="form__field">
            <p></p>
            <div className="form__input">
              <input
                type="text"
                name="activity"
                placeholder=""
                className="default__input"
                value={formData.activity ?? ''}
                onChange={handleChange}
              />
              <svg
                width="21"
                height="21"
                viewBox="0 0 21 21"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                {/* SVG path here */}
              </svg>
            </div>
          </div>

          {/*    */}
          <div className="form__field">
            <p></p>
            <div className="form__input">
              <input
                type="phone"
                name="phone"
                placeholder="Phone-number"
                className="default__input"
                value={formData.phone ?? ''}
                onChange={handleChange}
              />
              <svg
                width="21"
                height="21"
                viewBox="0 0 21 21"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                {/* SVG path here */}
              </svg>
            </div>
          </div>

          {/*   Instagram */}
          <div className="form__field">
            <p>Instagram</p>
            <div className="form__input">
              <input
                type="text"
                name="instagram"
                placeholder="Instagram"
                className="default__input"
                value={formData.instagram ?? ''}
                onChange={handleChange}
              />
              <svg
                width="21"
                height="21"
                viewBox="0 0 21 21"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                {/* SVG path here */}
              </svg>
            </div>
          </div>

          {/*   VK */}
          <div className="form__field">
            <p>VK</p>
            <div className="form__input">
              <input
                type="text"
                name="vk"
                placeholder="VK"
                className="default__input"
                value={formData.vk ?? ''}
                onChange={handleChange}
              />
              <svg
                width="21"
                height="21"
                viewBox="0 0 21 21"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                {/* SVG path here */}
              </svg>
            </div>
          </div>

          {/*   Telegram */}
          <div className="form__field">
            <p>Telegram</p>
            <div className="form__input">
              <input
                type="text"
                name="telegram"
                placeholder="Telegram"
                className="default__input"
                value={formData.telegram ?? ''}
                onChange={handleChange}
              />
              <svg
                width="21"
                height="21"
                viewBox="0 0 21 21"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                {/* SVG path here */}
              </svg>
            </div>
          </div>

          {/*   Facebook */}
          <div className="form__field">
            <p>Facebook</p>
            <div className="form__input">
              <input
                type="text"
                name="facebook"
                placeholder="Facebook"
                className="default__input"
                value={formData.facebook ?? ''}
                onChange={handleChange}
              />
              <svg
                width="21"
                height="21"
                viewBox="0 0 21 21"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                {/* SVG path here */}
              </svg>
            </div>
          </div>

          {/*    */}
          <div className="form__field">
            <p> </p>
            <textarea
              name="about"
              rows={3}
              placeholder="  "
              className='default_input' 
              value={formData.about ?? ''}
              onChange={handleChange}
            ></textarea>
          </div>

        {/*    (  ) */}
        <div className="form__field form-portfolio">
          <div className="flex-profile item-center gap-2">
            <p></p>
            <button className="article-upload article-upload-md" type="button">
              <label htmlFor="portfolio-file">
                <img src={addFile} alt=" " />
              </label>
            </button>
            <div className="form__input">
              <input
                id="portfolio-file"
                type="file"
                accept="image/*"
                hidden
                onChange={handlePortfolioImageChange}
              />
            </div>
          </div>
          <div className="images">
            {formData.portfolioImage && (
              <img
                src={`${apiUrl}/${formData.portfolioImage}`}
                alt="Portfolio"
                className="portfolio-image"
              />
            )}
          </div>
        </div>

          {/*      */}
          <div className="form__field">
            <input
              type="checkbox"
              name="receiveNewsletter"
              id="receive__message"
              checked={formData.receiveNewsletter}
              onChange={handleChange}
            />
            <label htmlFor="receive__message"> </label>
          </div>

          {/*    */}
          <button className="button__with__bg form__post" type="submit">
            
          </button>
        </div>
        {/*   */}
        <div className="user__stars">
          <div className="flex-profile item-center gap-2">
            {/*     */}
            {[...Array(5)].map((_, index) => (
              <img
                key={index}
                src={star}
                alt="Star"
                width={30}
                height={30}
              />
            ))}
          </div>
          <p>4.0</p>
        </div>
      </form>
    </section>
  );
};

export default EditProfileForm;
