import React, { useState, useEffect, ChangeEvent, FormEvent } from 'react';
import { useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify';
import axios from 'axios';
import { createArticle } from '../utils/apiService'; //  
import { ArticleFormData, ContentImage } from '../types'; //   FormData  types.ts
import addFile from '../assets/images/add-file.svg';
import deleteFile from '../assets/images/delete.png';
import 'react-toastify/dist/ReactToastify.css';
import './CreateArticleForm.css';

const CreateArticleForm: React.FC = () => {
  const [formData, setFormData] = useState<ArticleFormData>({
    title: '',
    content: '',
    coverImage: null as File | null,
    contentImages: [] as ContentImage[],
  });
  const [coverImagePreview, setCoverImagePreview] = useState<string | null>(null);
  const [contentImagesPreviews, setContentImagesPreviews] = useState<string[]>([]);  
  const navigate = useNavigate();

  // useEffect     formData
  useEffect(() => {
    if (formData.contentImages.length > 0) {
      //  contentImages ,     
      const formDataToSend = new FormData();
      formData.contentImages.forEach((contentImages) => {
        formDataToSend.append("files", contentImages.file);
      });

      // ,    
      fetch('/upload', {
        method: 'POST',
        body: formDataToSend,
      })
        .then(response => response.json())
        .then(data => console.log('  ', data))
        .catch(error => console.error('   ', error));
    }
  }, [formData.contentImages]); //       contentImages


  const handleChange = (e: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleImagesChange = async (e: ChangeEvent<HTMLInputElement>) => {
    const files = e.target.files;
    if (files && files.length > 0) {
      const validTypes = ['image/jpeg', 'image/png'];
      const validContentImages: { file: File; previewUrl: string }[] = []; //    URL
      const contentPreviews: string[] = [];
  
      const promises = Array.from(files).map(async (file) => {
        console.log(' :', file);
  
        //   
        if (!validTypes.includes(file.type)) {
          toast.error('    JPG  PNG.');
          console.log('  :', file.type);
          return;
        }
  
        //   
        if (file.size > 5 * 1024 * 1024) {
          toast.error('  . ,      5MB.');
          console.log('  :', file.size);
          return;
        }
  
        const img = new Image();
        const fileUrl = URL.createObjectURL(file);
  
        //   
        await new Promise<void>((resolve) => {
          img.src = fileUrl;
          img.onload = () => {
            console.log(' :', img.width, img.height);
            if (img.width > 1920 || img.height > 1080) {
              toast.error('   . ,       1920x1080.');
              console.log('   ');
              return;
            }
  
            //     URL
            validContentImages.push({ file, previewUrl: fileUrl });
            contentPreviews.push(fileUrl);
            resolve();
          };
        });
      });
  
      await Promise.all(promises);
  
      //     
      setFormData((prev) => ({
        ...prev,
        contentImages: [...prev.contentImages, ...validContentImages],
      }));
  
      //  
      setContentImagesPreviews((prev) => {
        const newPreviews = contentPreviews.filter((preview) => !prev.includes(preview));
        return [...prev, ...newPreviews];
      });
  
      //    ,   
      if (!coverImagePreview && contentPreviews.length > 0) {
        setCoverImagePreview(contentPreviews[0]);
        setFormData((prev) => ({
          ...prev,
          coverImage: validContentImages[0].file,
        }));
      }
    }
  };
  
  
  //   
  const handleCoverImageSelect = (imageFile: File) => {
    console.log(' : ', imageFile);  //   
    setCoverImagePreview(URL.createObjectURL(imageFile));  //  
    setFormData((prev) => ({
      ...prev,
      coverImage: imageFile,  //  coverImage  
    }));
  };
  
  //   
  const handleImageRemove = (image: string, e: React.MouseEvent<HTMLButtonElement>) => {
    //    
    e.preventDefault();
    e.stopPropagation();
  
    console.log('  :', image);
  
    //    
    const indexToRemove = contentImagesPreviews.findIndex((preview) => preview === image);
    console.log('   contentImagesPreviews:', indexToRemove);
  
    if (indexToRemove !== -1) {
      //     
      const updatedPreviews = [...contentImagesPreviews];
      updatedPreviews.splice(indexToRemove, 1);
      setContentImagesPreviews(updatedPreviews);
      console.log('   :', updatedPreviews);
    } else {
      console.log('     contentImagesPreviews  .');
    }
  
    //       
    const imageFileToRemove = formData.contentImages.find(
      (contentImage) => contentImage.previewUrl === image //   URL
    );
  
    console.log('     formData.contentImages...');
    if (imageFileToRemove) {
      console.log('   :', imageFileToRemove.file.name);
  
      //     contentImages
      const updatedContentImages = formData.contentImages.filter(
        (contentImage) => contentImage !== imageFileToRemove
      );
  
      setFormData((prev) => ({
        ...prev,
        contentImages: updatedContentImages,
      }));
      console.log(' contentImages  :', updatedContentImages);
    } else {
      console.log('    formData.contentImages.');
    }
  
    //    ,  
    if (coverImagePreview === image) {
      setCoverImagePreview(null);
      setFormData((prev) => ({
        ...prev,
        coverImage: null,
      }));
      console.log(' ');
    } else {
      console.log('   ');
    }
  };
  
  
  const handleSubmit = async (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();
  
    const token = localStorage.getItem('token');
    if (!token) {
      toast.error('  ');
      return;
    }
  
    if (formData.title.length < 5) {
      toast.error('     5 .');
      return;
    }
  
    if (formData.content.length < 50) {
      toast.error('      50 .');
      return;
    }
  
    const data = new FormData();
    data.append('title', formData.title);
    data.append('content', formData.content);
  
    //     
    if (formData.coverImage) {
      data.append('coverImage', formData.coverImage);
    }
  
    //     
    if (formData.contentImages && formData.contentImages.length > 0) {
      formData.contentImages.forEach((contentImages) => {
        data.append('contentImages', contentImages.file);
      });
    }
  
    try {
      const response = await createArticle(data, token);
      console.log('  :', response);
      toast.success('  !');
      setTimeout(() => {
        navigate('/articles');
      }, 2000);
    } catch (error: unknown) {
      if (axios.isAxiosError(error)) {
        console.error('   :', error.response?.data?.message || error.message);
        toast.error(error.response?.data?.message || '   . ,   .');
      } else if (error instanceof Error) {
        console.error('   :', error.message);
        toast.error(error.message);
      } else {
        console.error(' ', error);
        toast.error('  . ,   .');
      }
    }
  };
  
  return (
    <section className="container">
      <div className="create__article flex justify-center container">
        <div className="create__article__title">
          <h1 className="title main__title"> </h1>
        </div>
      </div>
      <form onSubmit={handleSubmit}>
        {/*   */}
        <div className="flex pt-2 item-center myInput">
          <label className="label"></label>
          <input
            id="title"
            type="text"
            name="title"
            className="default__input article-input"
            value={formData.title}
            onChange={handleChange}
            required
          />
        </div>
  
        {/*   */}
        <div className="pt-2 myInput">
          <div className="label desc__label">
            <label className="label"></label>
          </div>
          <textarea
            id="content"
            name="content"
            className="default__input article-input"
            rows={20}
            value={formData.content}
            onChange={handleChange}
            required
          ></textarea>
        </div>
  
        <form>
          {/*   */}
          <div className="field">
            <input
              id="imagesInput"
              type="file"
              name="images"
              accept="image/*"
              multiple
              onChange={handleImagesChange}
              style={{ display: 'none' }}
            />
            <button className="article-upload article-upload-md" type="button">
              <label htmlFor="imagesInput">
                <img src={addFile} alt=" " />
              </label>
            </button>

            {/*   */}
            {contentImagesPreviews.length > 0 && (
              <div className="images-preview" style={{ display: 'flex', flexWrap: 'wrap' }}>
                {contentImagesPreviews.map((imagePreview, index) => {
                  const contentImage = formData.contentImages[index];  //    ContentImage
                  const imageFile = contentImage.file;  //      ContentImage
                  const isCover = formData.coverImage?.name === imageFile.name;  //    

                  return (
                    <div
                      key={index}
                      style={{
                        margin: '10px',
                        display: 'inline-block',
                        background: 'transparent',
                        cursor: 'pointer',
                        position: 'relative',
                      }}
                    >
                      <img
                        src={imagePreview}
                        alt={`Preview ${index}`}
                        style={{
                          maxWidth: '200px',
                          maxHeight: '200px',
                          filter: 'opacity(0.5)',
                        }}
                        onClick={() => handleCoverImageSelect(imageFile)} //     
                      />
                      {/*   */}
                      <button
                        onClick={(e) => handleImageRemove(imagePreview, e)}
                        style={{
                          position: 'absolute',
                          background: 'transparent',
                          borderRadius: '50%',
                          padding: '5px',
                          cursor: 'pointer',
                          transform: 'translate(-45px, -45px) scale(0.5)',
                        }}
                      >
                        <img src={deleteFile} alt="" />
                      </button>

                      {/*      */}
                      {isCover && (
                        <div style={{
                          textAlign: 'center',
                          marginTop: '5px',
                          color: '#000000',
                        }}>
                          <span>  </span>
                        </div>
                      )}
                    </div>
                  );
                })}
              </div>
            )}
          </div>
        </form>
  
        {/*    */}
        <div className="flex pt-2 item-stretch myInput">  
          <div className="label"></div>
          <button
            className="button__with__bg article-btn"
            type="submit"
          >
            
          </button>
        </div>
      </form>
    </section>
  );  
};

export default CreateArticleForm;
