export const incrementArticleViews = async (id: string): Promise<void> => {
  try {
    const token = localStorage.getItem('token');
    if (!token) {
      toast.error(':   ');
      throw new Error('  ');
    }

    await axiosInstance.post(`${apiUrl}/api/articles/${id}/views`, {}, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    });
  } catch (error) {
    console.error('   :', error);
  }
};

